<template>
<div class="shopRecommend" id="shopRecommend" v-if="recommend && recommend.list && recommend.list.length>0">
	<div class="shopRecommendTitle">商家推荐</div>	
	<swiper :options="swiperShopTuiJianOption" class="swiperShopTuiJianOption">
		<swiper-slide v-for="(r,index) in products" :key="index">
			<button class="item">
				<img :src="r.icon" alt="" />
				<p class="name">{{r.name}}</p>
				<p class="info">
					<span>月售{{r.sales}}</span>
					<span class="haoping" v-if="r.applauseRate>0">好评{{r.applauseRate}}%</span>
				</p>
				<div class="foot">
					<span class="price">{{r.price}}</span>
					<span class="mark"></span>
					<div class="buy" v-if="r.stock>0">
						<button v-if="r.buyCount>0" class="less" @click="update(index,1)">
							<i class="iconfont icon-shanchu6"></i>
						</button>
						<span v-if="r.buyCount>0">{{r.buyCount}}</span>
						<button class="add" @click="update(index,2)"><i class="iconfont icon-jiahao2fill"></i></button>
					</div>
					<div class="buy" v-else>
						已售完
					</div>
				</div>
			</button>
		</swiper-slide>
	</swiper>
</div>
</template>

<script>
export default {
	name: 'shopRecommend',
	props:["shopId","recommend","goods"],
	data () {
		return {
			products:[],
			swiperShopTuiJianOption:{ //商家推荐的滑动特效
				slidesPerView: 3,
      			spaceBetween: 5,
			}
		}
	},
	created() {
		this.products=this.getGoodsList(this.recommend.list);
	},
	watch: {
		products(){
			return this.products;
		}
	},
	methods: {
		//返回商品列表 商家推荐
		getGoodsList (ids) {
			var prods=[];
			var list=this.goods;
			ids.forEach(id=>{
				list.forEach(item=>{
					if(id===item.id){
						prods.push(item);
					}
				})
			})
			return prods;
		},
		//往购物车减少/增加商品数量
		update(i,n){
			this.products.forEach((a,b)=>{
				(b===i && n===1) ? a.buyCount-- : (b===i && n===2) ? a.buyCount++ : "";
			})
		}
	}
}
</script>

<style scoped lang="less">
.shopRecommend{
	.shopRecommendTitle{margin:0 10px;padding:20px 0 5px;}
	.swiperShopTuiJianOption{margin:0 10px;
		.name{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;text-align: left;}
		.info{font-size: 10px;color:#999;text-align: left;
			span:first-child{margin-right:5px;}
		}
		.swiper-slide{box-shadow:0px 0px 2px #eee;box-sizing: border-box;}
		button.item{width:100%;display:block;
			p{margin-bottom: 5px;}
			img{width:100%;display:block;height: auto;}
			.foot{width:100%;display:flex;
				.price{width:50px;color: #ff5339;
					&:before{content:"￥";font-size: .8em;}
				}
				.buy{min-width:90px;float: right;text-align: right;
					span{font-size: .8em;position: relative;top:-2px;}
					button{
						i{color:#2396ff;}
					}
				}
			}
		}
	}
}
@media screen and (max-width:320px){
	.shopRecommend .swiperShopTuiJianOption .info span.haoping{display:none;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy{min-width:60px;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy button{padding:0;}
}
@media screen and (max-width:375px){
	.shopRecommend .swiperShopTuiJianOption .info span.haoping{display:none;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy{min-width:65px;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy button{padding:0;}
}
</style>


