<template>
<div class="shopCategory" id="shopCategory">
	<button v-for="(category,index) in arr" :key="index" :class="categoryNameStyle">
		<i></i>
		<span>{{category.name}}</span>
		<em></em>
	</button>
</div>
</template>

<script>
export default {
	name: 'shopCategory',
	props:["foods"],
	data () {
		return {
			arr:[],
			categoryNameStyle:[]
		}
	},
	created(){
		this.arr=this.getCategoryName(this.foods);
	},
	methods: {
		//获取商品左侧类目名称时，从 “热销” 开始
		getCategoryName (list) {
			let arr=[];
			list.forEach((item,index)=>{
				index>1 ? arr.push(item) : ""
			})
			return arr;
		}
	}
}
</script>

<style scoped lang="less">
.shopCategory{width:100px;background-color: #F8F8F8;
	button{
		display:block;width: 100%;height: 50px;line-height: 50px;position: relative;
		span{color: #666;display:block;text-align: left;padding-left: 10%;}
	}
}
</style>


