<template>
	<div class="shopHeader" v-if="shop">
		<header class="header pr">
			<img class="top" src="https://fuss10.elemecdn.com/c/e0/283549d417057f6829e829af1a2eapng.png" alt="">
			<button class="back" @click="$router.go(-1)"><i class="iconfont icon-fanhui1"></i></button>
			<button class="search"><i class="iconfont icon-sousuoleimu"></i></button>
			<button class="share"><i class="iconfont icon-skip1"></i></button>
			<button class="collect"><i class="iconfont icon-xihuanfill"></i></button>
			<span class="logo"><img v-if="shop.logo" :src="shop.logo" alt="" /><i v-if="shop.tag">{{shop.tag}}</i></span>
		</header>
		<section class="baseInfo pt50">
			<p class="name tc f18 mb10">{{shop.name}}</p>
			<p class="tc">
				<span v-if="shop.score">评价{{shop.score}}</span>
				<span v-if="shop.sales" class="ml10 mr10">月售{{shop.sales}}</span>
				<span v-if="shop.hummingbird">{{shop.hummingbird}}约{{shop.duration}}分钟</span>
			</p>
			<button class="adoptive" @click="switchInstruction" v-if="!showInstruction">
				<div class="w oh huodong">
					<div class="fl" v-if="getHuoDong(shop.offer).list && getHuoDong(shop.offer).list.length>0">
						<i class="youh">{{getHuoDong(shop.offer).name}}</i>
						<span v-for="(sol,index) in getHuoDong(shop.offer).list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fr">{{getYouHuiCount(shop.offer)}}个优惠<i class="iconfont icon-xiangxia2"></i></div>
				</div>
				<div class="gonggao" v-if="shop.announcement">公告：{{shop.announcement}}</div>
			</button>
		</section>

		<section class="instruction" v-show="showInstruction">
			<dl>
				<dt>优惠 <button class="shouqi" @click="switchInstruction"><i class="iconfont icon-xiangshang2"></i></button></dt>
				<dd class="youhui">
					<p v-for="(item,index) in shop.offer" v-if="item.list.length>0" :key="index">
						<i :class="youhuoStyle(item.name)">{{item.name}}</i>
						<span v-for="(subItem,index) in item.list" :key="index">{{subItem.name}}</span>
					</p>
				</dd>
			</dl>
			<dl v-if="shop.service.length>0">
				<dt>服务</dt>
				<dd class="fuwu">
					<p v-for="(s,index) in shop.service" :key="index">
						<i>{{s.name}}</i>
						<span>{{s.desc}}</span>
					</p>
				</dd>
			</dl>
			<dl v-if="shop.announcement">
				<dt>公告</dt>
				<dd class="gonggao">{{shop.announcement}}</dd>
			</dl>
		</section>
	</div>
</template>

<script>
export default {
	name: 'shopHeader',
	props:{
		shop: {
			type:Object
		}
	},
	data () {
		return {
			showInstruction:false
		}
	},
	methods: {
		//分别显示优惠样式
		youhuoStyle(i){
			let s="";
			switch(i){
				case "满减" :
				case "折扣" :
				case "特价" :
				case "红包" : s="youh"; break;
				case "新客" :
				case "首单" : s="shoudan"; break;
				case "会员" : s="huiyuan"; break;
			}
			return s;
		},
		//显示在公告上面的活动
		getHuoDong (list) {
			let obj={};
			for(let i=0;i<list.length;i++){
				if(list[i].list.length>0){
					obj=list[i];
					break;
				}
			}
			return obj;
		},
		// 切换显示优惠明细
		switchInstruction () {
			this.showInstruction = !this.showInstruction;
		},
		//返回优惠数量
		getYouHuiCount (objList) {
			let n=0;
			objList.forEach(item=>{
				item.list.length>0 ? n++ : "";
			})
			return n;
		}
	}
}
</script>

<style scoped lang="less">
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}

.shopHeader{
	.header button{position:absolute;top:10px;z-index:2;color:#fff;}
	.header button:hover{cursor:pointer;}
	.header .iconfont{font-size:1.5em;}
	.back .iconfont{left: 10px;font-size: 1.2em;}
	.share{right: 10px;}
	.search{right: 50px;}
	img.top{display: block;width: 100%;height: auto;}
	.header .collect{background-color: #fff;width: 30px;height: 30px;text-align: center;line-height: 30px;display:block;border-radius: 100%;top:auto;bottom: -15px;right: 20px;padding:0;box-shadow: 0px 3px 3px #ddd;
		i{color:#f55050;position: relative;top:3px;}
	}
	.logo{width: 100px;display:block;position:absolute;left: 50%;border-radius: 3px;margin-left: -50px;overflow:hidden;bottom: -25px;
		img{display:block;width: 100%;height: auto;border-radius: 3px;}
		i{font-style: normal;background-color: yellow;position: absolute;top:0;left: 0;z-index: 2;padding:0px 3px;border-radius: 3px 0 3px 0;}
	}
	.adoptive{width:100%;display:block;padding:15px 10px 5px;box-sizing: border-box;
		.huodong{height:30px;
			.fl{color: #666;
				i{font-style: normal;border-width:1px;border-style:solid;padding:1px 5px;border-radius: 3px;box-sizing: border-box;}
				.youh{color: #ce7b77;background-color: #fff4f2;border-color: #f4d3d1;}
				.shoudan{color: #768663;background-color: #eaf9e7;border-color: #d1dfc5;}
				.huiyuan{color: #b66d30;background-color: #fffef7;border-color: #e3d3b0;}
				span{margin-left: 5px}
			}
			.fr{font-size: .8em;color: #999;
				i{font-size: .8em;}
			}
		}
		.gonggao{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;color: #999;}	
	}
	.instruction{padding: 20px 10px;box-sizing: border-box;margin-bottom:50px;transition: all 0.3s;
		i{font-style: normal;}
		dl{margin-bottom: 10px;}
		dt{font-weight: bolder;height: 40px;line-height: 40px;color: #333;position: relative;
			.shouqi{position: absolute;right: 0;top: 0;z-index: 2;}
		}
		dd{font-size: .8em;color: #666;}
		.youhui{
			p{line-height: 20px;width:100%;margin-bottom: 10px;}
			i{float:left;border-width:1px;border-style:solid;padding:0 5px;height:21px;border-radius: 3px;box-sizing: border-box;margin-right: 5px;}
			.youh{color: #ce7b77;background-color: #fff4f2;border-color: #f4d3d1;}
			.shoudan{color: #768663;background-color: #eaf9e7;border-color: #d1dfc5;}
			.huiyuan{color: #b66d30;background-color: #fffef7;border-color: #e3d3b0;}

		}
		.fuwu{
			p{line-height: 20px;width:100%;margin-bottom: 10px;}
			i{float:left;border:1px solid #ddd;padding:0 5px;height:21px;border-radius: 3px;box-sizing: border-box;margin-right: 5px;}	
			span{word-wrap:break-word;word-break:break-all;line-height: 20px;display:table;}
		}
	}


}
</style>


