<template>
<div class="shopFoods">
	<shopTopic v-if="foods && foods[0] && foods[0].list && foods[0].list.length>0" :topic="foods[0]" :shopId="shopId"></shopTopic>
	<shopRecommend v-if="foods && foods[1] && foods[1].list && foods[1].list.length>0" :recommend="foods[1]" :goods="goods" :shopId="shopId"></shopRecommend>
	<div class="foods" :class="foodsFixed" ref="foods" id="foods">
		<div class="left" ref="foodsLeft">
			<shopCategory v-if="foods && foods.length>2" :foods="foods"></shopCategory>
		</div>
		<div class="right" ref="foodsRight">
			<shopGoods v-if="goods && goods.length>0" :goods="goods" :foods="foods" :shopId="shopId"></shopGoods>
		</div>
	</div>
</div>
</template>

<script>
import shopTopic from './topic.vue';
import shopRecommend from './recommend.vue';
import shopCategory from './category.vue';
import shopGoods from './goods.vue';
export default {
	name: 'shopFoods',
	props:["shopId","foods","goods"],
	components: {shopTopic,shopRecommend,shopCategory,shopGoods},
	data () {
		return {
			foodsFixed:''
		}
	},
	mounted () {
		this.$nextTick(() => {
			setTimeout(()=>{
				document.getElementById("foods").style.height=document.getElementById("shopCategory").clientHeight+document.getElementById("shopRecommend").clientHeight+20+"px";
			})
		})
	}
}
</script>

<style scoped lang="less">
.shopFoods{
	.foods{padding:20px 0 50px 0;display:flex;box-sizing:border-box;
		&.fixed{position: fixed;left: 50%;top:0;z-index: 10;width:100%;max-width: 640px;transform: translate(-50%,0);
			.left{height:100%;}
			.right{height:100%;overflow-y:auto;}
		}
		.left{width:100px;background-color: #F8F8F8;}
		.right{width:calc(100% - 100px);padding:0 0 0 10px;background-color: #fff;box-sizing:border-box;overflow-y:auto;}
	}
}
</style>


