<template>
	<div class="shopGoods" id="shopGoods">
		<section v-for="(item, index) in list" :key="index">
			<div class="head">{{item.name}} <span>{{item.desc}}</span></div>
			<button v-for="(prod, subIndex) in item.products" :key="subIndex" class="food">
				<img :src="prod.icon" alt="" />
				<div class="con">
					<p class="name">{{prod.name}}</p>
					<p class="desc">{{prod.desc}}</p>
					<p class="info">
						<span>月售{{prod.sales}}</span>
						<span v-if="prod.applauseRate>0">好评{{prod.applauseRate}}%</span>
					</p>
					<div class="foot">
						<span class="price">{{prod.price}}</span>
						<div class="buy">
							<button v-if="prod.buyCount>0" class="less" @click="update(index,subIndex,1)"><i class="iconfont icon-shanchu6"></i></button>
							<span v-if="prod.buyCount>0">{{prod.buyCount}}</span>
							<button class="add" @click="update(index,subIndex,2)"><i class="iconfont icon-jiahao2fill"></i></button>
						</div>
					</div>
				</div>
			</button>
		</section>
	</div>
</template>

<script>
export default {
	name: 'shopGoods',
	props:["shopId", "goods", "foods"],
	data () {
		return {
			list:[]
		}
	},
	created(){
		//先得到大类 及 所属ids
		var arr=this.getCategoryName(this.foods);
		//遍历大类里面ids，通过ids获取具体商品信息，组装在大类products里
		this.getGoodsList(arr);
	},
	watch: {
		list(){
			return this.list;
		}
	},
	mounted () {
		this.$nextTick(() => {
			setTimeout(() => {
				document.getElementById("shopGoods").style.height=document.getElementById("shopCategory").clientHeight+"px";
			})
		})
	},
	methods: {
		//遍历大类里面ids，通过ids获取具体商品信息，组装在大类products里
		getGoodsList (arr) {
			arr.forEach(item=>{
				item.products=[];
				item.list.forEach(subItem=>{
					this.goods.forEach(third=>{
						subItem===third.id ? item.products.push(third) : "";
					})
				})
			})
			this.list=arr;
		},
		//获取商品左侧类目名称时，从 “热销” 开始
		getCategoryName (list) {
			let arr=[];
			list.forEach((item,index)=>{
				index>1 ? arr.push(item) : ""
			})
			return arr;
		},
		//往购物车减少/增加商品数量
		update(a,b,n){
			this.list.forEach((item,index)=>{
				if(a===index){
					item.products.forEach((subItem,subIndex)=>{
						(b===subIndex && n===1) ? subItem.buyCount-- : (b===subIndex && n===2) ? subItem.buyCount++ : "";
					})
				}
			})
		}
	}
}
</script>

<style scoped lang="less">
.shopGoods{
	section{margin-bottom: 20px;}
	.head{
		span{color: #999;font-size: .8em;}
	}
	.food{width:100%;display:flex;padding:5px 0;box-sizing:border-box;
		img{width:25%;display:inline-block;}
		.con{width: 75%;display:inline-block;padding-left: 10px;box-sizing:border-box;
			p{margin-bottom: 3px;}
			.name,.desc,.info{text-align: left;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
			.desc,.info{color:#999;font-size: .8em;}
			.foot{width:100%;overflow:hidden;}
			.price{float:left;color: #ff5339;
				&:before{content:"￥";font-size: .8em;}
			}
			.buy{min-width:90px;float: right;text-align: right;
				span{font-size: .8em;position: relative;top:-2px;}
				button{
					i{color:#2396ff;}
				}
			}
		}
	}
}
</style>


