<template>
	<div class="shopBottomCart" :class="empty">
		<div class="icon">
			<i class="iconfont icon-gouwuche"></i>
			<span v-if="empty!=='empty'"></span>
		</div>
		<div class="total">
			<p class="payPrice">{{payPrice}}</p>
			<p class="freight" v-if="shop.shop.freight>0">另需配送费{{shop.shop.freight}}元</p>
		</div>
		<button class="buyBtn" @click="goPay">{{buyBtnText}}</button>
	</div>
</template>

<script>
export default {
	name: 'shopBottomCart',
	props:["shop"],
	data () {
		return {
			payPrice:"未选购商品",
			empty:"empty",//   notEnough
			buyBtnText:"", // 还差￥17起送    去结算
			elemeCart:{}
		}
	},
	created(){
		this.buyBtnText=`￥${this.shop.shop.startUpSend}起送`;
		if(localStorage.elemeCart){
			let objArr=JSON.parse(localStorage.elemeCart);
			for(let i=0;i<objArr.length;i++){
				if(objArr[i].shopId===this.shopId){
					this.elemeCart=objArr[i];
				}
			}
			if(!this.elemeCart.shopId){
				this.elemeCart.shopId=this.shopId;
				this.elemeCart.freight=this.shop.shop.freight;
				this.elemeCart.List=[];
				this.elemeCart.total=0;
			}
		}else{
			this.elemeCart.shopId=this.shopId;
			this.elemeCart.freight=this.shop.shop.freight;
			this.elemeCart.List=[];
			this.elemeCart.total=0;
		}
	},
	methods: {
		//点击去结算按钮事件
		goPay(){
			if(this.buyBtnText==="去结算"){
				this.$toast("正在加载");
			}
		}
	}
}
</script>

<style scoped lang="less">
.shopBottomCart{position:fixed;left: 0;bottom: 0;z-index: 10;width: 100%;height: 50px;display:flex;background-image:linear-gradient(left, #f86627, #e6291b); 
	&.empty{color:#999;background-color:rgba(0,0,0,0.8);background-image:none;
		.icon-gouwuche{color:#666;}
		.total,.payPrice{color:#999;}
		.payPrice{font-size: 1.1em;font-weight: normal;}
		.payPrice:before{content:"";}
		.buyBtn{background-color: #535356;color: #a9a9ab;}
	}
	&.notEnough{color:#fff;
		.icon-gouwuche{color:#fff;}
		.buyBtn{background-color: #ffbb39;color: #ffdd9c;}
	}
	.total{position: relative;left: 25%;padding-top: 5px;color:#fff;}
	.payPrice{margin-bottom: -3px;font-size: 1.5em;color:#fff;font-weight: bolder;}
	.payPrice:before{content:"￥";font-size: 10px;font-weight: normal;}
	.freight{font-size: 10px;}
	.icon{position: absolute;top:0;left: 5%;}
	.icon span{position: absolute;right: -10px;top:5px;z-index: 2;width:20px;height: 20px;text-align: center; background-color: #2396ff;color:#fff;border-radius: 100%;display:block;font-size: 10px;}
	.icon-gouwuche{font-size: 38px;position: relative;top:18px;color:#fff;}
	.buyBtn{position: absolute;top:0;right: 0;z-index: 1;width: 30%;height: 100%;background-color: #ffbb39;font-size:1.2em;color:#fff;font-weight:bolder;}
}
</style>


